import AssetManager from "../Managers/AssetManager";
import Item from "../Objects/Item";

export default class InventorySystem {

    private _savedItem:Item;

    //passed global
    stage:createjs.StageGL;

    constructor(stage:createjs.StageGL, assetManager:AssetManager) {
        
        //init global
        this.stage = stage;

        //init inventory sprite
    }
    // ----- gets/sets
    set savedItem(value:Item) { this._savedItem = value; }

    // ----- public methods

    public Update() {
        //display saved item
        let savedItemCheck:Item;
        if (savedItemCheck != this._savedItem) {
            this.stage.addChild(this._savedItem.sprite);
            this.savedItem.sprite.x = 10;
            this.savedItem.sprite.y = 10;
            savedItemCheck = this._savedItem;
        }
    }
} 